@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,700&display=swap&subset=cyrillic-ext");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

a {
  text-decoration: none;
}

.btn {
  width: 220px;
  height: 65px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  font-size: 18px;
  font-weight: bold;
  border: 1px solid rgba(0, 0, 0, 0.2);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: 0.3s all;
  outline: none;
}
.btn_white {
  background-color: #ffffff;
}
.btn_dark {
  background-color: #303030;
  color: #ffffff;
  border: none;
}
.btn_min {
  height: 50px;
}
.btn:hover {
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.3);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.divider {
  width: 330px;
  height: 1px;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.5);
}

.title {
  font-size: 36px;
  font-weight: 400;
}

.header {
  background-color: #373b6a;
  display: flex;
  justify-content: flex-end;
  padding: 20px;
}
.header__search {
  padding: 10px;
  border-radius: 20px;
  background-color: #373b6a;
  width: 200px;
  border: 1px solid #000000;
  font-size: 16px;
}

.header__search::placeholder {
  color: #5d629c;
}

.movies {
  background-color: #21254b;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
}